import { useHistory } from "react-router";

export default function ImageCard() {
  const history = useHistory();
  console.log("card:", history);
  const handleClick = () => {
    history.push("/pexels");
  };
  return (
    <>
      <button type="button" onClick={handleClick}>
        back
      </button>

      <h2>CARD</h2>
    </>
  );
}
